.app-container{
    h1{
        position: absolute;
        top: 10px;
        left: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgb(199,163,28);
        text-shadow: 3px 3px 4px #0000002e;
        img{
        height: 40px;
        width: 40px;
        
        }

    }

    .app-header{
        width: 90%;
        display: flex;
        margin: 100px auto 60px;
        justify-content: space-around;
        align-items: center;

        @media screen and (max-width:1100px) {
            display: block;
        }
    }

    .top{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        

        &:hover{
            transform: scale(1.05);
        }
    }
    overflow: hidden;

}

