.button-container{
    position:absolute;
    top: 20px;
    right: 20px;
    display: flex;
    justify-content: right;
    align-items: center;
    width: 50%;
    padding: 10px;
    border-radius: 20px;

    div{
        margin-left: 50px;
        cursor: pointer;

        label{
            cursor: pointer;
        }
    }

    .withStable{
        input{
            display: none;
        }
    }

    .list.active{
        color: rgb(199,163,28);
    }
    .fav-list.active{
        color: rgb(199,163,28);
    }

}