.tokens-container{
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 25% 75%;
    border-top:solid 1px  black;
    padding:20px 0  ;
    
    svg, img{
            height: 18px;
            width: 18px;

    }
    svg{
        transition: 0.2s;
        &:hover{
            color: aqua;
        }
    }
    
    

    .left-part{
        display: grid;
        grid-template-columns: 5% 5% 10% 80%;
        
        .staricon-container{
            margin-right: 2px;
        }

        p, .img, .info, .staricon-container{
            display: flex;
            align-items: center;
        }
        

        .info{
            display: flex;
            justify-content: start;           

            h2{
                font-size: 1.3rem;
                margin: 0 10px;

                span{
                    font-size: 0.6rem;
                    font-weight: 200;
                    color: rgba(255, 255, 255, 0.66);
                }
            }

            a{
                color: white;
                transition: 0.2s;
                

                &:hover{
                    transform: scale(1.05);
                    color: aqua;
                }
            }
        }
    }

    .right-part{
        display: grid;
        grid-template-columns: repeat(10, 10%);

        p, div{
            display: flex;
            align-items: center;
            font-size: 0.9rem;
            margin: 0 auto;
        }
    }
}