.main-container{
    .header{
        display: grid;
        grid-template-columns: 25% 75%;
        width: 90%;
        margin: 0 auto ;
        padding: 20px 0;

        &.active {

            .top{
                display: flex;
                justify-content: center;
                align-items: center;
            }
            
            position: fixed;
            width: 90%;
            top:-40px;
            left: 50%;
            transform: translateX(-50%);
            background-color: rgba(0, 0, 0, 0.165);
            backdrop-filter: blur(2px);
            animation: navAnim 0.3s forwards;
            

            @keyframes navAnim {
                to {
                    opacity: 1;
                    top: 0;
                }
            }
        }

        .input-container{
            display: grid;
            grid-template-columns: 33% 33% 33%;

            span{
                text-align: center;
                display: flex;
                align-items: center;
                #number{
                    width: 50%;
                }
            }

            input[type=number]::-webkit-outer-spin-button,
            input[type=number]::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            }
            input[type=number]{
                border: none;
                background: none;
                color: black;
                font-family: 'Kanit', sans-serif;
                font-size: 1.2rem;
                margin-left: 2px;

                &:focus{
                    outline: none;
                }
            }

            .range-container{
                margin-right: 5px;
                display: flex;
                align-items: center;
            }

            .top{
                display: none;
            }
            .top.active{
                display: flex;
                justify-content: center;
                align-items: center;

                @media screen and (max-width:1400px) {
                    display: none;
                }
            }
        }

        .headerTitle{
            display: grid;
            grid-template-columns: repeat(10, 10%);
            width: 100%;

            li{
                display: flex;
                align-items: center;
                margin: 0 auto;

                input{
                    display: none;

                    &:checked + label{
                        color: aqua;
                    }
                }
            }
        }
    }

    
}